@import url(https://fonts.googleapis.com/css?family=Lobster&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bangers&display=swap);
.App {
  margin: 5% 20% auto;
  padding: 0;
  box-sizing: border-box;
}

header {
  text-align: center;
  font-family: "Lobster", cursive;
  font-size: 3em;
}

.recipes {
  margin-top: 4em;
}

.recipe{border-bottom:1px solid black;margin:0.5em;font-family:"Bangers",cursive;padding:0.2em;overflow:hidden}.recipe .dropdown-menu{display:flex;justify-content:space-between}.recipe .dropdown-menu .dropdown-label{font-size:2em;letter-spacing:0.1em;margin-left:1.5em}.recipe .dropdown-menu .arrow-button{all:unset;margin-right:1em}.recipe .dropdown-detail{margin-top:2em;text-align:center}.recipe .dropdown-detail .recipe-image{width:15em;height:15em;border-radius:50%}.recipe .dropdown-detail .recipe-calories{font-size:2em}

.recipes{display:flex;flex-direction:column;width:100%;height:100%}

